import { Form, Formik } from "formik";
import React from "react";
import { Tooltip } from "react-bootstrap";
import { Select, Input as TextInput, UploadInput } from "../../Antd";
import { CommonButton } from "../../UiElement";
import { Media } from "../../../apiEndPoints";
import validation from "./validation";

function LoginForm({ onSubmit, loading, brandData }) {
  const initialValues = {
    name: "",
    image: "",
    categoryOrder: "",
    parentCategoryId: undefined,
  };

  return (
    <Formik
      initialValues={{ ...initialValues }}
      validationSchema={validation()}
      onSubmit={onSubmit}
      enableReinitialize
    >
      {(props) => {
        return (
          <Form>
            <div className="form-group">
              <div className="form-label-group">
                <label className="form-label" htmlFor="email">
                  Category Name
                </label>
              </div>
              <div className="form-control-wrap">
                <TextInput
                  id="name"
                  className="form-control form-control-lg"
                  name="name"
                  disabled={false}
                  floatingLabel={false}
                  variant="standard"
                  placeholder="Please Enter Category Name"
                  setFieldValue={props.handleChange}
                />
              </div>
            </div>
            <div className="form-group">
              <div className="form-label-group">
                <label className="form-label" htmlFor="email">
                  Category Order
                </label>
              </div>
              <div className="form-control-wrap">
                <TextInput
                  id="categoryOrder"
                  className="form-control form-control-lg "
                  name="categoryOrder"
                  disabled={false}
                  floatingLabel={false}
                  variant="standard"
                  placeholder="Please Enter Category Order"
                  setFieldValue={props.handleChange}
                />
              </div>
            </div>
            <div className="form-group">
              <div className="form-label-group">
                <label className="form-label" htmlFor="email">
                  Brand
                </label>
              </div>
              <div className="form-control-wrap">
                <Select
                  id="parentCategoryId"
                  name="parentCategoryId"
                  // disabled={false}s
                  variant="standard"
                  arrayOfData={brandData}
                  placeholder="Select Brand"
                  setFieldValue={props.handleChange}
                  // loanLoading={loanLoading}
                  //   onSelectChange={(e) => setCountryId(e)}
                />
              </div>
            </div>
            <div className="form-group">
              <div className="form-label-group">
                <label className="form-label" htmlFor="email">
                  Category Image
                </label>
              </div>
              <div className="form-control-wrap d-flex justify-content-center">
                <UploadInput
                  name="image"
                  apiEndPoints={Media.profileImage}
                  type="file"
                  // disabled={!isEdit}
                  defaultImageUrl=""
                  setFieldValue={props.handleChange}
                >
                  <Tooltip
                    placement="right"
                    color="#5D4BFF"
                    promptText="Upload either a JPEG, JPG or PNG file not exceeding 5MBs in size"
                  >
                    <label className="mb-0 ripple-effect filelabel">
                      <em className="icon ni ni-pen2" />
                    </label>
                  </Tooltip>
                </UploadInput>
              </div>
            </div>

            <div className="form-group d-flex justify-content-center">
              <CommonButton
                extraClassName="btn btn-primary"
                loading={loading}
                htmlType="submit"
                type="submit"
              >
                Add Product Category
              </CommonButton>
            </div>
          </Form>
        );
      }}
    </Formik>
  );
}

export default LoginForm;
